.btn::after{
  content: "";
  position: absolute;
  z-index: -1;
  background-color:#FADAA3;
  inset: 0;
  
  scale: 0 1 ;
  transform-origin: right;
  transition: scale 550ms;
}

.btn:hover::after,
.btn:focus-visible::after{
  
  transform-origin: left;
  scale: 1 1;
}

.btn2::after{
  content: "";
  position: absolute;
  z-index: -1;
  background-color:black;
  inset: 0;
  
  scale: 0 1 ;
  transform-origin: right;
  transition: scale 550ms;
}

.btn2:hover::after,
.btn2:focus-visible::after{
  
  transform-origin: left;
